import React from 'react';
import '../Styles/SampleProjectsListSample.scss';
import {Android, Apple, Web} from '@material-ui/icons';
import Button from '@material-ui/core/Button';


function SampleProjectsListSample(props) {
    [].forEach.call(document.querySelectorAll('img[data-src]'), function (img) {
        img.setAttribute('src', img.getAttribute('data-src'));
        img.onload = function () {
            img.removeAttribute('data-src');
        };
    });


    return (
        <div className="container">

            <img src={props.data.avatar} alt={props.data.alt} className="image"/>
            {/*<img className="image" alt={props.data.alt} data-src={props.data.avatar}/>*/}

            <div className="nameDiv">
                <h3 className="nametxt" >{props.data.name}</h3>

                {props.data.platform === 'android' ? <Android className="nameicon"/> : props.data.platform === 'ios' ? <Apple className="nameicon"/> : <Web className="nameicon"/>}
            </div>


            { props.data.link === "working"?

                <span style={{color: 'red'}}>In progress...</span>
                :

                props.data.link !== ""?
                    <Button variant="outlined" color="primary">
                        <a className="seeprBTN" target="_blank" rel="noopener noreferrer"
                           href={props.data.link}>see project</a>
                    </Button>

                    :
                    <span style={{color: 'red'}}>unpublished :(</span>
            }

        </div>
    );
}


export default SampleProjectsListSample;
